.meu-titulo {
  text-align: center;
  font-family: "Poiret One", cursive;
  font-size: 1.5rem;
  color: #ffffe6;
  text-transform: uppercase; }
  .meu-titulo hr {
    width: 40%; }
  .meu-titulo h6 {
    margin: 0;
    font-size: 1.5rem;
    text-transform: capitalize; }

.sobre-mim {
  display: flex;
  flex-direction: column;
  align-items: center; }
  .sobre-mim img {
    margin-top: 1rem;
    border-radius: 50%;
    width: 11rem;
    height: 11rem;
    box-shadow: 2px 0px 5px black; }
  .sobre-mim p {
    max-width: 70%;
    font-size: 1rem;
    line-height: 2rem;
    text-align: left;
    padding: 1rem 1rem; }

@media only screen and (max-width: 800px) {
  .sobre-mim img {
    max-width: 25rem; }
  .sobre-mim p {
    width: 100%; } }
